html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
body {
  -webkit-overflow-scrolling: touch;
  font: 14px/1.6 "Helvetica Neue", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif;
}
body .weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon,
body .weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon > i,
body .weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #fc9400;
}
body .weui-tabbar:before {
  border-top: none;
}
body .weui-tabbar {
  position: fixed;
}
body .child-view {
  width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  -webkit-transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
  transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
}
body .slide-left-enter,
body .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100vw, 0);
  transform: translate(100vw, 0);
}
body .slide-left-leave-active,
body .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-100vw, 0);
  transform: translate(-100vw, 0);
}
body .app-div {
  position: fixed;
  width: 100vw;
  top: 0px;
  bottom: 0px;
  z-index: 999999999;
  text-align: center;
  background: rgba(255, 255, 255, 0.89);
}
body .app-div img {
  width: 10%;
  margin-top: 50%;
}
body .app-div p {
  color: #fc9400;
}
body .vux-slider > .vux-indicator > a > .vux-icon-dot.active,
body .vux-slider .vux-indicator-right > a > .vux-icon-dot.active {
  background-color: #fc9400;
}
body .vux-popup-header-right {
  color: #fc9400;
}
body .dp-header .dp-item.dp-right {
  color: #fc9400;
}
body .weui-icon-warn {
  display: none !important;
}
